* {
  background-color: rgb(25, 5, 68);
  margin: 0;
  padding: 0;
}
.App {
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(25, 5, 68);
  color: white;
}
.button {
  margin: 10px;
  background-color: yellow;
  padding: 10px 30px;
  border-radius: 10px;
  text-decoration: none;
  color: rgb(25, 5, 68);
}
.hw_card {
  display: flex;
  margin: 10px;
  background-color: yellow;
  width: 50%;
  height: 30%;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  box-shadow: 0 0 10px white;
}
.hw_card *{
  max-width: 100%;
  background-color: yellow;
  color: rgb(25, 5, 68);
}